@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Poppins", sans-serif;
}

.container {
  max-width: 500px;

  margin: 0 auto;
  overflow: auto;
  min-height: 100vh;
  border: 1px solid steelblue;
  padding: 30px;
  border-radius: 5px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:focus {
  outline: none;
}

.btn:active {
  transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}

.current-date {
  margin: 2em auto;
  display: flex;
}
.container-text-icon {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.task {
  background: #f4f4f4;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
  animation: moveToRight 1s ease-in-out;
}

@keyframes moveToRight {
  0% {
    transform: translateX(-500px);
  }
  40% {
    transform: translateX(-480px);
  }
  100% {
    transform: translateX(0px);
  }
}
.task.reminder {
  border-left: 5px solid green;
}

.task h3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.add-form {
  margin-bottom: 40px;
}

.form-control {
  margin: 20px 0;
}

.form-control label {
  display: block;
}

.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}

.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-control-check label {
  flex: 1;
}

.form-control-check input {
  flex: 2;
  height: 20px;
}
.about {
  text-align: center;
  margin: 3em auto;
}

.active-tasks {
  margin: 2em auto;
}

footer {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0);
  margin: 1em auto;
  display: flex;
  flex-direction: column;
  text-align: center;
}

/*aux classes*/

.red {
  background: rgba(255, 0, 0, 0.13);
  color: red;
}
.text-center {
  justify-content: center;
}
